export default {
  name: 'cMonth',
  props: {
    current: {
      type: Number,
      default: () => {
        return 0
      }
    },
    list: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  methods: {
    handleCurrent (index, id) {
      if (this.current !== index) {
        this.$emit('onCurrent', index, id)
      }
    }
  }
}



